.stars {
  display: flex;
  align-items: center;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;

    &:last-child {
      margin: 0;
    }
  }

  @for $starCount from 1 through 5 {
    &--#{$starCount} .stars__star:nth-child(-n + #{$starCount}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
